import $ from 'jquery'
const baseUrl = 'http://localhost:8080/Library_war_exploded'

const login = (user) => {
    return $.post(`${baseUrl}/AdminServlet`, {
        adminId: user.username,
        adminPwd: user.passowrd,
        flag: 'login'
    }, (response) => {
        return response
    })
}

export default { login }