import { Button, Col, Input, Row } from "antd"
import { useState } from "react"
import { useHistory } from "react-router-dom"
import loginService from "../services/login"

const Login = (props) => {
    const history = useHistory()

    const [username, setUsername] = useState('')
    const handleUsername = (event) => {
        setUsername(event.target.value)
    }

    const [password, setPassword] = useState('')
    const handlePassword = (event) => {
        setPassword(event.target.value)
    }

    const handleClick = (event) => {
        event.preventDefault()
        const user = {
            username: username,
            password: password
        }
        loginService
            .login(user)
            .then(result => {
                if (result === 1) {
                    alert("登录成功")
                    props.onLogin(username)
                    history.push('/')
                } else if (result === 0) {
                    alert('账号或密码错误')
                }
            })
            .catch(error => {
                alert('服务器出现错误，请稍后再试')
            })
    }

    return (
        <div>
            <Row>
                <Col span={4} offset={10}>
                    <h1>登录</h1>
                </Col>
            </Row>
            <Row>
                <Col span={4} offset={10}>
                    账号:<Input placeholder="请输入账号" value={username} onChange={handleUsername} />
                </Col>
            </Row>
            <Row>
                <Col span={4} offset={10}>
                    密码:<Input.Password placeholder="请输入密码" value={password} onChange={handlePassword} />
                </Col>
            </Row>
            <Row>
                <Col offset={10}>
                    <Button type="primary" onClick={handleClick} style={{ margin: '24px auto'}}>登录</Button>
                </Col>
            </Row>
        </div>
    )
}

export default Login