import './App.css';
import { useState } from 'react';
import { Link, Route, Switch, Redirect } from 'react-router-dom';
import Login from './components/Login';
import User from './components/User';
import Books from './components/Books';
import Basics from './components/Basics';
import Buy from './components/Buy';
import { Layout, Menu } from 'antd';
import { Header } from 'antd/lib/layout/layout';

function App() {
    const [userid, setUserid] = useState(null)

    const login = (username) => {
        setUserid(username)
    }

    return (
        <Layout>
            <Header className='header'>
                <Menu theme='dark' mode='horizontal' defaultSelectedKeys={['1']}>
                    <Menu.Item key='1'>
                        <Link to='/'>图书信息管理</Link>
                    </Menu.Item>
                    <Menu.Item key='2'>
                        <Link to='/basic'>基础信息管理</Link>
                    </Menu.Item>
                    <Menu.Item key='3'>
                        <Link to='/buy'>荐购信息审核</Link>
                    </Menu.Item>
                    <Menu.Item key='4'>
                        <Link to='/more'>更多</Link>
                    </Menu.Item>
                    {userid &&
                        <Menu.Item key='5' style={{float:'right'}}>
                            <Link to='/user'>{userid}</Link>
                        </Menu.Item>
                    }
                </Menu>
            </Header>

            <Switch>
                <Route path='/user'>
                    {userid ? <User /> : <Redirect to='/login' />}
                </Route>
                <Route path='/login'>
                    <Login onLogin={login} />
                </Route>
                <Route path='/basic'>
                    {userid ? <Basics /> : <Redirect to='/login' />}
                </Route>
                <Route path='/buy'>
                    {userid ? <Buy /> : <Redirect to='/login' />}
                </Route>
                <Route path='/'>
                    {userid ? <Books /> : <Redirect to='/login' />}
                </Route>
            </Switch>
        </Layout>
    );
}

export default App;
